@import '../../styles/mixins';

.canvas-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  
  &__header {
    @include flex-between;
    align-items: flex-start;
    margin-bottom: spacing(4);
  }
  
  &__title {
    h4 {
      @include text-style(xl, semibold, tight);
      color: color(text, primary);
      margin-bottom: spacing(2);
    }
    
    p {
      @include text-style(sm, normal);
      color: color(text, tertiary);
      margin: 0;
    }
  }
  
  &__actions {
    @include flex-center;
    gap: spacing(2);
  }
}

.canvas {
  flex: 1;
  width: 100%;
  max-width: 100%;
  height: 550px;
  background: color(background, secondary);
  border: 2px dashed color(border, medium);
  border-radius: map-get($border-radius, xl);
  position: relative;
  overflow: auto; // Enable both horizontal and vertical scrolling
  transition: transition(colors);
  
  &--drag-over {
    border-color: color(primary, 500);
    background: color(primary, 50);
    
    .canvas__grid {
      background-image: 
        linear-gradient(rgba(124, 58, 237, 0.25) 1px, transparent 1px),
        linear-gradient(90deg, rgba(124, 58, 237, 0.25) 1px, transparent 1px);
    }
  }
  
  &--empty {
    @include flex-center;
  }

  &__scrollable-content {
    position: relative;
    height: 100%;
    min-height: 550px;
  }
  
  &__empty-state {
    text-align: center;
    padding: spacing(8);
    
    &-icon {
      @include flex-center;
      width: 80px;
      height: 80px;
      background: white;
      border: 1px solid color(border, light);
      border-radius: map-get($border-radius, full);
      margin: 0 auto spacing(6);
      color: color(text, tertiary);
      box-shadow: shadow(sm);
    }
    
    h5 {
      @include text-style(lg, semibold, tight);
      color: color(text, secondary);
      margin-bottom: spacing(3);
    }
    
    p {
      @include text-style(sm, normal, relaxed);
      color: color(text, tertiary);
      max-width: 300px;
      margin: 0 auto;
    }
  }
  
  &__connectors {
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 550px;
  }
  
  &__notes {
    position: relative;
    width: 100%;
    height: 100%;
    pointer-events: none; // Allow clicking through to canvas
    
    .note {
      pointer-events: auto; // Re-enable pointer events for notes
    }
  }
  
  &__connections {
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none; // SVG overlay doesn't block interactions
    z-index: 0; // Below connectors and notes
    
    .connection-endpoint {
      pointer-events: auto; // But endpoints should be clickable
    }
  }
  
  &__grid {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    opacity: 0.25;
    background-image: 
      linear-gradient(rgba(124, 58, 237, 0.15) 1px, transparent 1px),
      linear-gradient(90deg, rgba(124, 58, 237, 0.15) 1px, transparent 1px);
    background-size: 20px 20px;
    background-position: 0 0, 0 0;
  }
}

.sortable-connector {
  width: 140px; // Default to compact size
  z-index: 1;
  
  &:hover {
    z-index: 2;
  }
  
  &--target {
    z-index: 5;
    
    &:hover {
      z-index: 6;
    }
  }
  
  &--source {
    z-index: 3;
    
    &:hover {
      z-index: 4;
    }
  }
  
  &--expanded {
    z-index: 10;
    width: auto; // Let expanded item determine its own width
    
    .connector-item {
      animation: expandConnector 300ms cubic-bezier(0.4, 0, 0.2, 1);
    }
  }
}

@keyframes expandConnector {
  0% {
    transform: scale(0.95);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.connection-path {
  
  &:hover {
    stroke: rgba(124, 58, 237, 1);
    stroke-width: 3;
  }
  
  &--connected {
    stroke: rgba(124, 58, 237, 0.8);
    stroke-dasharray: none;
  }
  
  &--unconnected {
    stroke: rgba(124, 58, 237, 0.4);
    stroke-dasharray: 4,4;
  }
}

.connection-endpoint {
  
  &:hover {
    fill: rgba(124, 58, 237, 0.8);
    stroke-width: 3;
    cursor: pointer;
  }
  
  &:active {
    fill: rgba(124, 58, 237, 1);
  }
}

.canvas-action-btn {
  @include flex-center;
  gap: spacing(1);
  padding: spacing(2) spacing(3);
  background: white;
  border: 1px solid color(border, medium);
  border-radius: map-get($border-radius, md);
  color: color(text, secondary);
  @include text-style(sm, medium);
  cursor: pointer;
  transition: transition(colors);
  
  &:hover {
    background: color(background, secondary);
    border-color: color(primary, 300);
    color: color(primary, 600);
  }
  
  svg {
    color: currentColor;
  }
}
