@import '../../styles/mixins';

.connector-item {
  background: white;
  border: 1px solid color(border, light);
  border-radius: map-get($border-radius, lg);
  padding: spacing(4);
  box-shadow: shadow(sm);
  cursor: grab;
  user-select: none;
  width: 100%;
  
  &:hover {
    box-shadow: shadow(md);
    border-color: var(--connector-color, #{color(primary, 300)});
    transform: translateY(-1px);
    transition: transform 0.2s ease, box-shadow 0.2s ease, border-color 0.2s ease;
  }
  
  &--dragging {
    box-shadow: shadow(xl);
    z-index: map-get($z-index, modal);
    cursor: grabbing;
  }
  
  &--target {
    border-color: color(primary, 500);
    box-shadow: 0 0 0 3px rgba(124, 58, 237, 0.3);
    cursor: crosshair;
  }
  
  &--source {
    border-color: color(accent, blue);
    box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.3);
  }
  
  &--compact {
    width: 140px;
    padding: spacing(2);
    min-height: 80px;
    cursor: pointer;
    
    .connector-item__header {
      margin-bottom: spacing(1);
      gap: spacing(2);
    }
    
    .connector-item__icon {
      width: 32px;
      height: 32px;
    }
    
    .connector-item__name {
      @include text-style(xs, medium);
      margin-bottom: 0;
    }
    
    .connector-item__connection-point {
      width: 16px;
      height: 16px;
      right: -4px;
    }
  }
  
  &--expanded {
    width: 320px;
    z-index: map-get($z-index, modal);
    box-shadow: shadow(xl);
    transform: scale(1.02);
    
    .connector-item__connection-point {
      width: 20px;
      height: 20px;
      right: -8px;
    }
  }
  
  &__header {
    @include flex-between;
    align-items: flex-start;
    margin-bottom: spacing(3);
    gap: spacing(3);
  }
  
  &__icon {
    @include flex-center;
    width: 40px;
    height: 40px;
    background: var(--connector-bg, #{color(primary, 50)});
    color: var(--connector-color, #{color(primary, 600)});
    border-radius: map-get($border-radius, md);
    flex-shrink: 0;
    border: 1px solid var(--connector-color, #{color(primary, 200)});
  }
  
  &__info {
    flex: 1;
    min-width: 0;
  }
  
  &__name {
    @include text-style(sm, semibold, tight);
    color: color(text, primary);
    margin-bottom: spacing(1);
    @include truncate;
  }
  
  &__category {
    @include text-style(xs, medium);
    color: var(--connector-color, #{color(primary, 600)});
    background: var(--connector-bg, #{color(primary, 50)});
    padding: spacing(1) spacing(2);
    border-radius: map-get($border-radius, full);
    text-transform: uppercase;
    letter-spacing: map-get(map-get($typography, letter-spacings), wide);
    border: 1px solid var(--connector-color, #{color(primary, 200)});
  }
  
  
  
  &__connection-point {
    position: absolute;
    top: 50%;
    right: -6px;
    transform: translateY(-50%);
    @include flex-center;
    width: 24px;
    height: 24px;
    background: var(--connector-color, #{color(primary, 600)});
    color: white;
    border: 2px solid white;
    border-radius: 50%;
    cursor: pointer;
    z-index: 10;
    
    &:hover {
      transform: translateY(-50%) scale(1.2);
      box-shadow: shadow(md);
    }
    
    &:focus {
      @include focus-ring;
    }
  }
  
  &__description {
    @include text-style(xs, normal, relaxed);
    color: color(text, secondary);
    margin-bottom: spacing(3);
    line-height: 1.4;
  }
  
  &__features {
    display: flex;
    flex-wrap: wrap;
    gap: spacing(1.5);
  }
  
  &__feature {
    @include text-style(xs, medium);
    color: color(text, tertiary);
    background: color(background, secondary);
    padding: spacing(1) spacing(2);
    border-radius: map-get($border-radius, sm);
    border: 1px solid color(border, light);
    
    &--more {
      color: var(--connector-color, #{color(primary, 600)});
      background: var(--connector-bg, #{color(primary, 50)});
      border-color: var(--connector-color, #{color(primary, 200)});
    }
  }
}

.connector-item__compact-hint {
  @include text-style(xs, normal);
  color: color(text, tertiary);
  text-align: center;
  font-style: italic;
  margin-top: spacing(1);
}

// Sortable connector remove button styles
.sortable-connector {
  &__remove {
    position: absolute;
    top: calc(spacing(2) * -1);
    right: calc(spacing(2) * -1);
    display: block;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    background: rgba(255, 255, 255, 0.9);
    border: 1px solid color(border, light);
    color: color(text, tertiary);
    cursor: pointer;
    border-radius: 100%;
    transition: transition(colors);
    z-index: 10;
    padding: 2px 4px 4px 4px;

    &:hover {
      background: color(error);
      color: white;
      border-color: color(error);
    }

    &:focus {
      @include focus-ring;
    }

    svg {
      width: 12px;
      height: 12px;
    }
  }
}
