@import '../../styles/mixins';

.drag-drop-canvas {
  padding: spacing(8) spacing(10);
  
  &__header {
    @include container;
    @include flex-between;
    align-items: flex-start;
    margin-bottom: spacing(8);
    gap: spacing(6);
    
    h3 {
      @include text-style('3xl', bold, tight);
      margin-bottom: spacing(2);
      @include text-gradient-purple;
    }
    
    p {
      @include text-style(lg, normal, relaxed);
      color: color(text, secondary);
      margin: 0;
    }
    
    @include mobile-only {
      flex-direction: column;
      align-items: stretch;
      gap: spacing(4);
    }
  }
  
  &__actions {
    @include flex-center;
    gap: spacing(4);
    flex-shrink: 0;
    
    @include mobile-only {
      justify-content: space-between;
    }
  }
  
  &__count {
    @include text-style(sm, medium);
    color: color(text, tertiary);
    background: color(background, secondary);
    padding: spacing(2) spacing(3);
    border-radius: map-get($border-radius, full);
    border: 1px solid color(border, light);
  }
  
  &__workspace {
    @include container;
    display: flex;
    gap: spacing(8);
    min-height: 660px;
    overflow-x: auto;
    overflow-y: hidden;
    scroll-behavior: smooth;
    max-width: 100vw; // Ensure it doesn't exceed viewport
    
    // Custom scrollbar styling
    &::-webkit-scrollbar {
      height: 8px;
    }
    
    &::-webkit-scrollbar-track {
      background: color(background, secondary);
      border-radius: map-get($border-radius, full);
    }
    
    &::-webkit-scrollbar-thumb {
      background: color(border, medium);
      border-radius: map-get($border-radius, full);
      
      &:hover {
        background: color(primary, 400);
      }
    }
    
    @include tablet-up {
      gap: spacing(6);
    }
    
    @include mobile-only {
      flex-direction: column;
      gap: spacing(6);
      min-height: auto;
      overflow-x: visible;
    }
  }
}
