@import '../../styles/mixins';

.connector-palette {
  background: white;
  border: 1px solid color(border, light);
  border-radius: map-get($border-radius, xl);
  height: fit-content;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  box-shadow: shadow(sm);
  width: 320px;
  flex-shrink: 0; // Prevent shrinking in flex layout
  
  @include responsive(lg) {
    width: 380px;
  }
  
  @include tablet-up {
    width: 300px;
  }
  
  @include mobile-only {
    width: 100%;
    flex-shrink: 1;
  }

  // Collapsed state - minified connectors
  &--collapsed {
    width: 200px; // Smaller width for minified connectors
    
    @include tablet-up {
      width: 180px;
    }
    
    @include mobile-only {
      width: 100%;
    }

    .connector-palette__category {
      &-items {
        display: flex;
        flex-direction: column;
        gap: spacing(1); // Tighter spacing for minified items
      }
    }
  }
  
  &__header {
    @include flex-between;
    padding: spacing(6);
    border-bottom: 1px solid color(border, light);
    
    &-content {
      flex: 1;
      
      h4 {
        @include text-style(xl, semibold, tight);
        color: color(text, primary);
        margin-bottom: spacing(2);
      }
      
      p {
        @include text-style(sm, normal);
        color: color(text, tertiary);
        margin: 0;
      }
    }
  }

  &__toggle {
    display: block;
    padding: 4px 0 0 0;
    margin-left: 8px;
    width: 32px;
    height: 32px;
    border: none;
    background: transparent;
    border-radius: map-get($border-radius, md);
    color: color(text, secondary);
    cursor: pointer;
    transition: transition(colors);
    flex-shrink: 0;

    &:hover {
      background: color(background, tertiary);
      color: color(text, primary);
    }

    &:active {
      background: color(primary, 50);
      color: color(primary, 600);
    }
  }
  
  &__content {
    flex: 1;
    overflow-y: auto;
    padding: spacing(4);
    
    // Custom scrollbar
    &::-webkit-scrollbar {
      width: 6px;
    }
    
    &::-webkit-scrollbar-track {
      background: color(background, secondary);
      border-radius: map-get($border-radius, full);
    }
    
    &::-webkit-scrollbar-thumb {
      background: color(border, medium);
      border-radius: map-get($border-radius, full);
      
      &:hover {
        background: color(primary, 400);
      }
    }
  }
  
  &__category {
    &:not(:last-child) {
      margin-bottom: spacing(6);
    }
    
    &-title {
      @include text-style(sm, semibold);
      color: color(text, secondary);
      text-transform: uppercase;
      letter-spacing: map-get(map-get($typography, letter-spacings), wide);
      margin-bottom: spacing(3);
      padding-bottom: spacing(2);
      border-bottom: 1px solid color(border, light);
    }
    
    &-items {
      display: flex;
      flex-direction: column;
      gap: spacing(2);
    }
  }
  
  &__item {
    cursor: grab;

    &:active {
      cursor: grabbing;
    }
    
    &.dragging {
      transform: rotate(2deg);
    }
    
    &:hover {
      transform: translateY(-2px);
      transition: transform 0.2s ease;
    }

    // Minified state for collapsed palette
    &--minified {
      .connector-item {
        // Ensure compact styling is applied
        min-height: auto;
        
        &__header {
          padding: spacing(2);
        }
        
        &__name {
          @include text-style(xs, medium);
        }
        
        &__icon {
          width: 16px;
          height: 16px;
        }
      }
    }
  }
}
