@import '../../styles/mixins';

.rearrangeable-tabs {
  padding: spacing(8) 0;
  
  &__header {
    @include container;
    @include flex-between;
    align-items: flex-start;
    margin-bottom: spacing(8);
    gap: spacing(6);
    
    h3 {
      @include text-style('3xl', bold, tight);
      margin-bottom: spacing(2);
      @include text-gradient-purple;
    }
    
    p {
      @include text-style(lg, normal, relaxed);
      color: color(text, secondary);
      margin: 0;
    }
    
    @include mobile-only {
      flex-direction: column;
      align-items: stretch;
      gap: spacing(4);
    }
  }
  
  &__stats {
    @include flex-center;
    gap: spacing(4);
    flex-shrink: 0;
  }
  
  &__count {
    @include text-style(sm, medium);
    color: color(text, tertiary);
    background: color(background, secondary);
    padding: spacing(2) spacing(3);
    border-radius: map-get($border-radius, full);
    border: 1px solid color(border, light);
  }
  
  &__container {
    @include container;
    padding-left: 0;
    padding-right: 0;
    max-width: 1250px;
    background: white;
    border: 1px solid color(border, light);
    border-radius: map-get($border-radius, xl);
    box-shadow: shadow(sm);
    overflow: hidden;
  }
  
  &__nav {
    border-bottom: 1px solid color(border, light);
    background: color(background, secondary);
  }
  
  &__list {
    display: flex;
    overflow-x: auto;
    scrollbar-width: none;
    -ms-overflow-style: none;
    
    &::-webkit-scrollbar {
      display: none;
    }
    
    @include mobile-only {
      padding: 0 spacing(2);
    }
  }
  
  &__content {
    min-height: 500px;
  }
}

.sortable-tab {
  display: flex;
  align-items: center;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: transition(colors);
  white-space: nowrap;
  flex-shrink: 0;
  position: relative;
  
  &:hover {
    background: rgba(107, 114, 128, 0.08);
  }
  
  &--active {
    background: rgba(124, 58, 237, 0.05);
    
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 2px;
      background: color(primary, 600);
    }
  }
  
  &--dragging {
    opacity: 0.5;
    transform: rotate(2deg);
    z-index: map-get($z-index, modal);
  }
  
  &--pinned {
    
    &::before {
      content: '';
      position: absolute;
      top: spacing(1);
      left: spacing(1);
      width: 4px;
      height: 4px;
      background: color(primary, 500);
      border-radius: 50%;
    }
  }
  
  &__content {
    @include flex-center;
    gap: spacing(2);
    padding: spacing(4) spacing(4);
    min-height: 60px;
    
    @include mobile-only {
      padding: spacing(3) spacing(4);
      gap: spacing(1.5);
    }
  }
  
  &__drag-handle {
    @include flex-center;
    color: color(text, tertiary);
    cursor: grab;
    opacity: 0;
    transition: transition(opacity);
    
    .sortable-tab:hover & {
      opacity: 1;
    }
    
    &:active {
      cursor: grabbing;
    }
  }
  
  &__icon {
    @include flex-center;
    width: 20px;
    height: 20px;
    color: color(text, secondary);
    
    .sortable-tab--active & {
      color: color(primary, 600);
    }
  }
  
  &__title {
    @include text-style(sm, medium);
    color: color(text, secondary);
    
    .sortable-tab--active & {
      color: color(text, primary);
      font-weight: map-get(map-get($typography, font-weights), semibold);
    }
  }
  
  &__actions {
    display: contents;
    gap: spacing(1);
    opacity: 0;
    transition: transition(opacity);
    
    .sortable-tab:hover & {
      opacity: 1;
    }
  }
  
  &__pin {
    display: block;
    width: 24px;
    height: 24px;
    background: none;
    border: none;
    color: color(text, tertiary);
    cursor: pointer;
    border-radius: map-get($border-radius, sm);
    transition: all 200ms ease;
    padding: 6px 0;
    
    &:hover {
      color: color(primary, 600);
    }

    &:hover > svg {
      transform: scale(1.1);
    }
    
    &--active {
      color: color(primary, 600);
      background: color(primary, 100);
      
      &:hover {
        background: color(primary, 200);
        transform: scale(1.1);
      }
    }
  }
}
