// SCSS Mixins for Component Portfolio
// ===================================

@import 'tokens';

// String replace function
@function str-replace($string, $search, $replace: '') {
  $index: str-index($string, $search);
  
  @if $index {
    @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
  }
  
  @return $string;
}

// Helper function to get color values
@function color($color, $shade: 500) {
  $color-map: map-get($colors, $color);
  @if type-of($color-map) == 'map' {
    @return map-get($color-map, $shade);
  } @else {
    @return $color-map;
  }
}

@function spacing($size) {
  @return map-get($spacing, $size);
}

@function shadow($type) {
  @return map-get($shadows, $type);
}

@function transition($type: default) {
  @return map-get($transitions, $type);
}

// Typography Mixins
// -----------------
@mixin text-style($size: base, $weight: normal, $line-height: normal) {
  font-size: map-get(map-get($typography, font-sizes), $size);
  font-weight: map-get(map-get($typography, font-weights), $weight);
  line-height: map-get(map-get($typography, line-heights), $line-height);
}

@mixin font-display {
  font-family: map-get(map-get($typography, font-families), display);
}

@mixin font-mono {
  font-family: map-get(map-get($typography, font-families), mono);
}

// Layout Mixins
// -------------
@mixin container($size: xl) {
  width: 100%;
  max-width: map-get($containers, $size);
  margin-left: auto;
  margin-right: auto;
  padding-left: spacing(4);
  padding-right: spacing(4);
}

@mixin flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin flex-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@mixin grid-center {
  display: grid;
  place-items: center;
}

// Card Components
// ---------------
@mixin card($elevated: false) {
  background: color(background, primary);
  border-radius: map-get($border-radius, lg);
  border: 1px solid color(border, light);
  
  @if $elevated {
    box-shadow: shadow(lg);
  } @else {
    box-shadow: shadow(sm);
  }
  
  transition: transition(shadow);
  
  &:hover {
    @if $elevated {
      box-shadow: shadow(xl);
    } @else {
      box-shadow: shadow(md);
    }
  }
}

@mixin card-purple {
  @include card(true);
  border: 1px solid color(primary, 200);
  box-shadow: shadow(purple-md);
  
  &:hover {
    box-shadow: shadow(purple-lg);
    border-color: color(primary, 300);
  }
}

// Button Mixins
// -------------
@mixin button-base {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: spacing(2);
  padding: spacing(2.5) spacing(4);
  border-radius: map-get($border-radius, md);
  @include text-style(sm, medium);
  text-decoration: none;
  cursor: pointer;
  transition: transition(colors);
  border: 1px solid transparent;
  
  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

@mixin button-primary {
  @include button-base;
  background: color(primary, 600);
  color: white;
  
  &:hover:not(:disabled) {
    background: color(primary, 700);
  }
  
  &:active {
    background: color(primary, 800);
  }
}

@mixin button-secondary {
  @include button-base;
  background: white;
  color: color(primary, 600);
  border-color: color(border, medium);
  
  &:hover:not(:disabled) {
    background: color(primary, 50);
    border-color: color(primary, 300);
  }
}

@mixin button-ghost {
  @include button-base;
  background: transparent;
  color: color(text, secondary);
  
  &:hover:not(:disabled) {
    background: color(background, secondary);
    color: color(text, primary);
  }
}

// Input Components
// ----------------
@mixin input-base {
  width: 100%;
  padding: spacing(2.5) spacing(3);
  border: 1px solid color(border, medium);
  border-radius: map-get($border-radius, md);
  @include text-style(sm);
  background: white;
  transition: transition(colors);
  
  &:focus {
    outline: none;
    border-color: color(primary, 500);
    box-shadow: 0 0 0 3px rgba(161, 110, 255, 0.1);
  }
  
  &::placeholder {
    color: color(text, tertiary);
  }
  
  &:disabled {
    background: color(background, secondary);
    color: color(text, tertiary);
    cursor: not-allowed;
  }
}

// Animation Mixins
// ----------------
@mixin fade-in($duration: 300ms, $delay: 0ms) {
  opacity: 0;
  animation: fadeIn $duration map-get($easings, out) $delay forwards;
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

@mixin slide-in-up($duration: 300ms, $delay: 0ms) {
  opacity: 0;
  transform: translateY(20px);
  animation: slideInUp $duration map-get($easings, out) $delay forwards;
}

@keyframes slideInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@mixin scale-in($duration: 200ms, $delay: 0ms) {
  opacity: 0;
  transform: scale(0.95);
  animation: scaleIn $duration map-get($easings, out) $delay forwards;
}

@keyframes scaleIn {
  to {
    opacity: 1;
    transform: scale(1);
  }
}

// Drag and Drop Mixins
// --------------------
@mixin draggable-item {
  cursor: grab;
  user-select: none;
  transition: transition(transform);
  
  &:active {
    cursor: grabbing;
  }
  
  &.dragging {
    opacity: 0.5;
    transform: rotate(5deg);
    z-index: map-get($z-index, modal);
  }
}

@mixin drop-zone {
  border: 2px dashed color(border, medium);
  border-radius: map-get($border-radius, lg);
  background: color(background, secondary);
  transition: transition(colors);
  
  &.drag-over {
    border-color: color(primary, 500);
    background: color(primary, 50);
  }
}

// Responsive Mixins
// -----------------
@mixin responsive($breakpoint) {
  @media (min-width: map-get($breakpoints, $breakpoint)) {
    @content;
  }
}

@mixin mobile-only {
  @media (max-width: map-get($breakpoints, sm) - 1px) {
    @content;
  }
}

@mixin tablet-up {
  @media (min-width: map-get($breakpoints, md)) {
    @content;
  }
}

@mixin desktop-up {
  @media (min-width: map-get($breakpoints, lg)) {
    @content;
  }
}

// Utility Mixins
// --------------
@mixin truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@mixin visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

@mixin focus-ring {
  outline: none;
  box-shadow: 0 0 0 3px rgba(161, 110, 255, 0.3);
}

// Glass Morphism Effect
// ---------------------
@mixin glass-morphism($opacity: 0.1) {
  background: rgba(255, 255, 255, $opacity);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

// Gradient Mixins
// ---------------
@mixin gradient-purple {
  background: linear-gradient(135deg, color(primary, 600), color(primary, 800));
}

@mixin gradient-purple-light {
  background: linear-gradient(135deg, color(primary, 50), color(primary, 100));
}

@mixin text-gradient-purple {
  background: linear-gradient(135deg, color(primary, 700), color(primary, 900));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
