@import '../../styles/mixins';

.tab-content {
  padding: spacing(8);
  
  @include mobile-only {
    padding: spacing(6) spacing(4);
  }
  
  &__header {
    margin-bottom: spacing(8);
  }
  
  &__title-section {
    max-width: 600px;
  }
  
  &__title {
    @include text-style('2xl', bold, tight);
    color: color(text, primary);
    margin-bottom: spacing(3);
  }
  
  &__description {
    @include text-style(base, normal, relaxed);
    color: color(text, secondary);
    margin: 0;
  }
  
  &__grid {
    display: grid;
    gap: spacing(8);
    
    @include responsive(lg) {
      grid-template-columns: 2fr 1fr;
      grid-template-rows: auto auto;
    }
  }
  
  &__section-title {
    @include text-style(lg, semibold, tight);
    color: color(text, primary);
    margin-bottom: spacing(4);
    padding-bottom: spacing(2);
    border-bottom: 2px solid color(primary, 200);
    display: inline-block;
  }
  
  &__stats {
    @include responsive(lg) {
      grid-column: 1;
      grid-row: 1;
    }
  }
  
  &__features {
    @include responsive(lg) {
      grid-column: 2;
      grid-row: 1 / 3;
    }
  }
  
  &__code {
    @include responsive(lg) {
      grid-column: 1;
      grid-row: 2;
    }
  }
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: spacing(4);
  
  @include mobile-only {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: spacing(3);
  }
}

.stat-card {
  @include card;
  padding: spacing(5);
  
  &__header {
    @include flex-between;
    align-items: flex-start;
    margin-bottom: spacing(3);
    gap: spacing(2);
  }
  
  &__label {
    @include text-style(sm, medium);
    color: color(text, secondary);
    flex: 1;
  }
  
  &__change {
    @include flex-center;
    gap: spacing(1);
    flex-shrink: 0;
  }
  
  &__change-value {
    @include text-style(xs, semibold);
    
    &--up {
      color: color(success);
    }
    
    &--down {
      color: color(error);
    }
    
    &--neutral {
      color: color(text, tertiary);
    }
  }
  
  &__value {
    @include text-style('2xl', bold, tight);
    color: color(text, primary);
  }
}

.trend-icon {
  &--up {
    color: color(success);
  }
  
  &--down {
    color: color(error);
  }
  
  &--neutral {
    color: color(text, tertiary);
  }
}

.features-list {
  display: flex;
  flex-direction: column;
  gap: spacing(3);
}

.feature-item {
  @include flex-center;
  align-items: flex-start;
  gap: spacing(3);
  padding: spacing(3);
  background: color(background, secondary);
  border-radius: map-get($border-radius, md);
  border: 1px solid color(border, light);
  transition: transition(colors);
  
  &:hover {
    background: color(primary, 50);
    border-color: color(primary, 200);
  }
  
  &__icon {
    color: color(primary, 600);
    margin-top: spacing(0.5);
    flex-shrink: 0;
  }
  
  &__text {
    @include text-style(sm, normal, relaxed);
    color: color(text, secondary);
    flex: 1;
  }
}

.code-block {
  background: color(secondary, 900);
  border-radius: map-get($border-radius, lg);
  overflow: hidden;
  border: 1px solid color(border, dark);
  
  pre {
    padding: spacing(6);
    margin: 0;
    overflow-x: auto;
    background: none;
    border: none;
    
    code {
      @include font-mono;
      @include text-style(sm);
      color: color(secondary, 100);
      background: none;
      padding: 0;
      white-space: pre;
      line-height: 1.6;
    }
  }
  
  // Syntax highlighting colors
  .keyword {
    color: #c792ea;
  }
  
  .string {
    color: #c3e88d;
  }
  
  .comment {
    color: #546e7a;
    font-style: italic;
  }
  
  .function {
    color: #82aaff;
  }
  
  .variable {
    color: #f07178;
  }
}
