@import '../../styles/mixins';

.note {
  width: 200px;
  min-height: 100px;
  background: #fef3c7; // Yellow note color
  border: 1px solid #f59e0b;
  border-radius: map-get($border-radius, md);
  box-shadow: shadow(md);
  font-family: 'Comic Sans MS', cursive, sans-serif;
  user-select: none;
  z-index: 10;
  transition: transform 200ms ease, box-shadow 200ms ease;
  
  &:hover {
    transform: scale(1.02);
    box-shadow: shadow(lg);
  }
  
  &--dragging {
    transform: scale(1.05) rotate(2deg);
    box-shadow: shadow(xl);
    z-index: 20;
  }
  
  &--editing {
    background: #fef3c7;
    border-color: color(primary, 400);
    box-shadow: 0 0 0 2px rgba(124, 58, 237, 0.2), shadow(lg);
    transform: scale(1.02);
  }
  
  &__header {
    display: flex;
    justify-content: flex-end;
    padding: spacing(2);
    padding-bottom: 0;
  }
  
  &__actions {
    @include flex-center;
    gap: spacing(1);
    opacity: 0;
    transition: opacity 200ms ease;
    
    .note:hover & {
      opacity: 1;
    }
    
    .note--editing & {
      opacity: 1;
    }
  }
  
  &__action-btn {
    display: block;
    width: 20px;
    height: 20px;
    background: transparent;
    color: color(text, secondary);
    cursor: pointer;
    transition: all 200ms ease;
    padding: 0;
    margin-right: 4px;
    
    &:hover {
      transform: scale(1.1);
      border-color: color(text, secondary);
    }
    
    &--remove {
      &:hover {
        color: color(error);
        border-color: color(error);
      }
    }
    
    &--save {
      &:hover {
        color: color(success);
        border-color: color(success);
      }
    }
    
    &--cancel {
      &:hover {
        color: color(warning-dark);
        border-color: color(warning-dark);
      }
    }
  }
  
  &__content {
    padding: spacing(2);
    padding-top: spacing(1);
  }
  
  &__text {
    @include text-style(sm, normal, relaxed);
    color: color(text, primary);
    min-height: 40px;
    word-wrap: break-word;
    white-space: pre-wrap;
    
    &:empty::before {
      content: 'Empty note';
      color: color(text, tertiary);
      font-style: italic;
    }
  }
  
  &__textarea {
    width: 100%;
    min-height: 60px;
    background: rgba(255, 255, 255, 0.5);
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: map-get($border-radius, sm);
    padding: spacing(2);
    @include text-style(sm, normal, relaxed);
    color: color(text, primary);
    font-family: inherit;
    resize: vertical;
    
    &:focus {
      outline: none;
      border-color: color(primary, 400);
      background: rgba(255, 255, 255, 0.8);
    }
    
    &::placeholder {
      color: color(text, tertiary);
      font-style: italic;
    }
  }
  
  &__hint {
    padding: 0 spacing(2) spacing(2);
    @include text-style(xs, normal);
    color: color(text, tertiary);
    font-style: italic;
    text-align: center;
  }
}

// Animation for new notes
@keyframes noteAppear {
  0% {
    transform: scale(0) rotate(-10deg);
    opacity: 0;
  }
  50% {
    transform: scale(1.1) rotate(5deg);
  }
  100% {
    transform: scale(1) rotate(0deg);
    opacity: 1;
  }
}

.note--new {
  animation: noteAppear 400ms ease-out;
}
