@import '../../styles/mixins';

.paginated-table {
  padding: spacing(8) 0;
  
  &__header {
    @include container;
    @include flex-between;
    align-items: flex-start;
    margin-bottom: spacing(8);
    gap: spacing(6);
    
    h3 {
      @include text-style('3xl', bold, tight);
      margin-bottom: spacing(2);
      @include text-gradient-purple;
    }
    
    p {
      @include text-style(lg, normal, relaxed);
      color: color(text, secondary);
      margin: 0;
    }
    
    @include mobile-only {
      flex-direction: column;
      align-items: stretch;
      gap: spacing(4);
    }
  }
  
  &__stats {
    @include flex-center;
    gap: spacing(4);
    flex-shrink: 0;
  }
  
  &__count {
    @include text-style(sm, medium);
    color: color(text, tertiary);
    background: color(background, secondary);
    padding: spacing(2) spacing(3);
    border-radius: map-get($border-radius, full);
    border: 1px solid color(border, light);
  }
  
  &__selected {
    @include text-style(sm, medium);
    color: white;
    background: color(primary, 600);
    padding: spacing(2) spacing(3);
    border-radius: map-get($border-radius, full);
  }
  
  &__container {
    background: white;
    border: 1px solid color(border, light);
    border-radius: map-get($border-radius, xl);
    box-shadow: shadow(sm);
    overflow: hidden;
    margin: 0 spacing(4);
    
    @include responsive(lg) {
      @include container;
      margin: 0 auto;
      padding-left: 0;
      padding-right: 0;
      max-width: 1250px;
    }
  }
}

.table-controls {
  @include flex-between;
  padding: spacing(6);
  border-bottom: 1px solid color(border, light);
  background: color(background, secondary);
  gap: spacing(4);
  
  @include mobile-only {
    flex-direction: column;
    align-items: stretch;
  }
  
  &__left,
  &__right {
    @include flex-center;
    gap: spacing(3);
    
    @include mobile-only {
      justify-content: space-between;
    }
  }
  
  &__button {
    @include flex-center;
    gap: spacing(2);
    padding: spacing(2) spacing(3);
    white-space: nowrap;
  }
}

.search-input {
  position: relative;
  
  &__icon {
    position: absolute;
    left: spacing(3);
    top: 50%;
    transform: translateY(-50%);
    color: color(text, tertiary);
  }
  
  &__field {
    @include input-base;
    padding-left: spacing(10);
    width: 300px;
    
    @include mobile-only {
      width: 100%;
    }
  }
}

.table-wrapper {
  overflow-x: auto;
}

.data-table {
  width: 100%;
  border-collapse: collapse;
  
  &__header {
    @include text-style(sm, semibold);
    color: color(text, secondary);
    text-align: left;
    padding: spacing(4) spacing(6);
    background: color(background, tertiary);
    border-bottom: 1px solid color(border, light);
    white-space: nowrap;
    
    &--sortable {
      cursor: pointer;
      user-select: none;
      transition: transition(colors);
      
      &:hover {
        background: color(primary, 50);
        color: color(primary, 700);
      }
    }
    
    &--checkbox {
      width: 60px;
      text-align: center;
    }
  }
  
  &__row {
    transition: transition(colors);
    
    &:hover {
      background: color(background, secondary);
    }
    
    &--selected {
      background: color(primary, 50);
      
      &:hover {
        background: color(primary, 100);
      }
    }
  }
  
  &__cell {
    padding: spacing(4) spacing(6);
    border-bottom: 1px solid color(border, light);
    @include text-style(sm, normal);
    color: color(text, primary);
    vertical-align: middle;
    max-width: 220px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    
    &--checkbox {
      text-align: center;
    }
  }
}

.sort-icon {
  margin-left: spacing(2);
  transition: transition(colors);
  
  &--inactive {
    color: color(text, tertiary);
  }
  
  &--active {
    color: color(primary, 600);
  }
}

.user-info {
  @include flex-center;
  gap: spacing(3);
  justify-content: flex-start;
  
  &__avatar {
    @include flex-center;
    width: 40px;
    height: 40px;
    background: color(primary, 600);
    color: white;
    border-radius: map-get($border-radius, full);
    @include text-style(sm, semibold);
    flex-shrink: 0;
  }
  
  &__details {
    display: flex;
    flex-direction: column;
    gap: spacing(0.5);
    min-width: 0;
  }
  
  &__name {
    @include text-style(sm, medium);
    color: color(text, primary);
    @include truncate;
  }
  
  &__id {
    @include text-style(xs, normal);
    color: color(text, tertiary);
  }
}

.role-badge {
  @include text-style(xs, medium);
  background: color(background, tertiary);
  color: color(text, secondary);
  padding: spacing(1.5) spacing(3);
  border-radius: map-get($border-radius, full);
  border: 1px solid color(border, light);
  text-transform: uppercase;
  letter-spacing: map-get(map-get($typography, letter-spacings), wide);
}

.status-badge {
  @include text-style(xs, semibold);
  padding: spacing(1.5) spacing(3);
  border-radius: map-get($border-radius, full);
  text-transform: uppercase;
  letter-spacing: map-get(map-get($typography, letter-spacings), wide);
  
  &.status--success {
    background: rgba(16, 185, 129, 0.1);
    color: #059669;
    border: 1px solid rgba(16, 185, 129, 0.2);
  }
  
  &.status--error {
    background: rgba(239, 68, 68, 0.1);
    color: #dc2626;
    border: 1px solid rgba(239, 68, 68, 0.2);
  }
  
  &.status--warning {
    background: rgba(245, 158, 11, 0.1);
    color: #d97706;
    border: 1px solid rgba(245, 158, 11, 0.2);
  }
  
  &.status--neutral {
    background: color(background, tertiary);
    color: color(text, secondary);
    border: 1px solid color(border, light);
  }
}

.score-indicator {
  @include flex-center;
  gap: spacing(2);
  
  &__bar {
    width: 60px;
    height: 8px;
    background: color(background, tertiary);
    border-radius: map-get($border-radius, full);
    overflow: hidden;
    flex-shrink: 0;
  }
  
  &__fill {
    height: 100%;
    background: linear-gradient(90deg, color(primary, 500), color(primary, 600));
    border-radius: map-get($border-radius, full);
    transition: width 0.3s ease;
  }
  
  &__text {
    @include text-style(xs, medium);
    color: color(text, secondary);
    min-width: 35px;
  }
}

.table-actions {
  @include flex-center;
  gap: spacing(2);
  
  &__button {
    @include text-style(xs, medium);
    padding: spacing(1.5) spacing(3);
    border: 1px solid color(border, medium);
    border-radius: map-get($border-radius, sm);
    background: white;
    color: color(text, secondary);
    cursor: pointer;
    transition: transition(colors);
    
    &:hover {
      background: color(background, secondary);
      border-color: color(border, dark);
    }
    
    &--danger {
      color: color(error);
      border-color: rgba(239, 68, 68, 0.3);
      
      &:hover {
        background: rgba(239, 68, 68, 0.05);
        border-color: color(error);
      }
    }
  }
}

.checkbox {
  width: 16px;
  height: 16px;
  accent-color: color(primary, 600);
  cursor: pointer;
}

.pagination {
  @include flex-between;
  padding: spacing(6);
  background: color(background, secondary);
  gap: spacing(4);
  
  @include mobile-only {
    flex-direction: column;
    align-items: stretch;
    gap: spacing(4);
  }
  
  &__info {
    @include flex-center;
    gap: spacing(4);
    @include text-style(sm, normal);
    color: color(text, secondary);
    
    @include mobile-only {
      justify-content: space-between;
    }
  }
  
  &__select {
    @include input-base;
    padding: spacing(2) spacing(3);
    width: auto;
    min-width: 120px;
  }
  
  &__controls {
    @include flex-center;
    gap: spacing(2);
  }
  
  &__button {
    display: contents;
    width: 36px;
    height: 36px;
    background: white;
    border: 1px solid color(border, medium);
    border-radius: map-get($border-radius, md);
    color: color(text, secondary);
    cursor: pointer;
    transition: transition(colors);
    
    &:hover:not(:disabled) {
      background: color(primary, 50);
      border-color: color(primary, 300);
      color: color(primary, 600);
    }
    
    &:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }
  }
  
  &__pages {
    @include flex-center;
    gap: spacing(1);
  }
  
  &__ellipsis {
    @include flex-center;
    width: 36px;
    height: 36px;
    color: color(text, tertiary);
    @include text-style(sm, medium);
  }
  
  &__page {
    @include flex-center;
    width: 36px;
    height: 36px;
    background: white;
    border: 1px solid color(border, medium);
    border-radius: map-get($border-radius, md);
    color: color(text, secondary);
    cursor: pointer;
    transition: transition(colors);
    @include text-style(sm, medium);
    
    &:hover {
      background: color(primary, 50);
      border-color: color(primary, 300);
      color: color(primary, 600);
    }
    
    &--active {
      background: color(primary, 600);
      border-color: color(primary, 600);
      color: white;
      
      &:hover {
        background: color(primary, 700);
        border-color: color(primary, 700);
        color: white;
      }
    }
  }
}

// Loading animation
.spinning {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
