@import '../../styles/mixins';

.footer {
  background: color(secondary, 900);
  color: color(secondary, 200);
  padding: spacing(16) 0 spacing(8);
  
  &__content {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: spacing(12);
    margin-bottom: spacing(12);
    
    @include responsive(lg) {
      gap: spacing(16);
    }
    
    @include tablet-up {
      grid-template-columns: 1fr;
      gap: spacing(10);
    }
  }
  
  // About Me Section
  &__about {
    max-width: 500px;
    
    @include tablet-up {
      text-align: center;
      max-width: none;
    }
  }
  
  &__about-title {
    @include text-style('2xl', bold, tight);
    color: white;
    margin-bottom: spacing(4);
    background: color(primary, 500);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  &__about-description {
    @include text-style(base, normal, relaxed);
    color: color(secondary, 300);
    line-height: 1.7;
  }
  
  // Contact Section
  &__contact {
    max-width: 300px;
    
    @include tablet-up {
      text-align: center;
      max-width: none;
      margin: 0 auto;
    }
  }
  
  &__contact-title {
    @include text-style(base, semibold, tight);
    color: white;
    margin-bottom: spacing(6);
    text-transform: uppercase;
    letter-spacing: map-get(map-get($typography, letter-spacings), wide);
  }
  
  &__contact-list {
    display: flex;
    flex-direction: column;
    gap: spacing(4);
  }
  
  &__contact-item {
    display: flex;
    flex-direction: column;
    gap: spacing(1);
    align-items: flex-start;
    
    @include tablet-up {
      align-items: center;
    }
  }
  
  &__contact-label {
    @include text-style(xs, medium);
    color: color(secondary, 400);
    text-transform: uppercase;
    letter-spacing: map-get(map-get($typography, letter-spacings), wide);
  }
  
  &__contact-link {
    @include text-style(sm, normal);
    color: color(secondary, 200);
    transition: transition(colors);
    display: inline-flex;
    align-items: center;
    gap: spacing(2);
    white-space: nowrap;
    
    &:hover {
      color: color(primary, 400);
      text-decoration: none;
    }
    
    svg {
      flex-shrink: 0;
    }
  }
  
  &__logo {
    @include flex-center;
    gap: spacing(3);
    margin-bottom: spacing(6);
    
    @include tablet-up {
      justify-content: center;
    }
    
    &-icon {
      @include flex-center;
      width: 40px;
      height: 40px;
      background: color(primary, 600);
      color: white;
      border-radius: map-get($border-radius, lg);
    }
    
    &-text {
      @include text-style(xl, bold);
      color: white;
    }
  }
  
  &__description {
    @include text-style(base, normal, relaxed);
    color: color(secondary, 300);
    margin-bottom: spacing(8);
    
    @include tablet-up {
      text-align: center;
    }
  }
  
  &__social {
    @include flex-center;
    gap: spacing(4);
    
    @include tablet-up {
      justify-content: center;
    }
  }
  
  &__social-link {
    @include flex-center;
    width: 40px;
    height: 40px;
    background: color(secondary, 800);
    color: color(secondary, 300);
    border-radius: map-get($border-radius, md);
    transition: transition(colors);
    
    &:hover {
      background: color(primary, 600);
      color: white;
      text-decoration: none;
    }
  }
  
  &__links {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: spacing(8);
    
    @include mobile-only {
      grid-template-columns: 1fr;
      gap: spacing(6);
    }
  }
  
  &__link-section {
    @include mobile-only {
      text-align: center;
    }
  }
  
  &__link-title {
    @include text-style(base, semibold, tight);
    color: white;
    margin-bottom: spacing(4);
    text-transform: uppercase;
    letter-spacing: map-get(map-get($typography, letter-spacings), wide);
  }
  
  &__link-list {
    list-style: none;
    padding: 0;
    margin: 0;
    
    li {
      margin-bottom: spacing(3);
      
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  
  &__link {
    @include text-style(sm, normal);
    color: color(secondary, 300);
    transition: transition(colors);
    
    &:hover {
      color: color(primary, 400);
      text-decoration: none;
    }
  }
  
  &__bottom {
    padding-top: spacing(8);
    border-top: 1px solid color(secondary, 800);
  }
  
  &__bottom-content {
    @include flex-between;
    align-items: center;
    gap: spacing(6);
    
    @include tablet-up {
      flex-direction: column;
      text-align: center;
      gap: spacing(4);
    }
  }
  
  &__copyright {
    @include text-style(sm, normal);
    color: color(secondary, 400);
    margin: 0;
  }
  
  &__bottom-links {
    @include flex-center;
    gap: spacing(6);
    
    @include mobile-only {
      flex-direction: column;
      gap: spacing(3);
    }
  }
  
  &__bottom-link {
    @include text-style(sm, normal);
    color: color(secondary, 400);
    transition: transition(colors);
    
    &:hover {
      color: color(primary, 400);
      text-decoration: none;
    }
  }
}
