@import '../../styles/tokens';
@import '../../styles/mixins';

.configuration-panel {
  position: absolute;
  top: 0;
  right: 0;
  width: 300px;
  height: 100%;
  background: color(background, primary);
  border-left: 1px solid color(border, light);
  box-shadow: shadow(xl);
  z-index: 100; // High enough to be above connectors but within canvas context
  display: flex;
  flex-direction: column;
  transform: translateX(0);
  transition: transition(transform);

  @include mobile-only {
    width: 100%;
    left: 0;
    right: 0;
  }

  &__header {
    @include flex-between;
    padding: spacing(4);
    border-bottom: 1px solid color(border, light);
    background: color(background, secondary);
  }

  &__title {
    display: flex;
    align-items: center;
    gap: spacing(3);
  }

  &__icon {
    @include flex-center;
    width: 40px;
    height: 40px;
    border-radius: map-get($border-radius, lg);
    background: color(primary, 600);
    color: white;
  }

  &__name {
    margin: 0;
    @include text-style(lg, semibold);
    color: color(text, primary);
  }

  &__category {
    margin: 0;
    @include text-style(sm);
    color: color(text, secondary);
  }

  &__close {
    display: block;
    padding: 4px 0 0 0;
    width: 32px;
    height: 32px;
    border: none;
    background: transparent;
    border-radius: map-get($border-radius, md);
    color: color(text, secondary);
    cursor: pointer;
    transition: transition(colors);

    &:hover {
      background: color(background, tertiary);
      color: color(text, primary);
    }
  }

  &__content {
    flex: 1;
    padding: spacing(4);
    overflow-y: auto;
  }

  &__description {
    margin: 0 0 spacing(4) 0;
    color: color(text, secondary);
    @include text-style(sm, normal, normal);
  }

  &__footer {
    display: flex;
    gap: spacing(2);
    padding: spacing(4);
    border-top: 1px solid color(border, light);
    background: color(background, secondary);
  }

  &__button {
    flex: 1;
    @include button-base;

    &--primary {
      @include button-primary;
    }

    &--secondary {
      @include button-secondary;
    }
  }
}

.config-form {
  &__group {
    margin-bottom: spacing(4);

    &--checkbox {
      margin-bottom: spacing(3);
    }
  }

  &__label {
    display: block;
    margin-bottom: spacing(2);
    @include text-style(sm, medium);
    color: color(text, primary);
  }

  &__input,
  &__select,
  &__textarea {
    @include input-base;
  }

  &__textarea {
    resize: vertical;
    min-height: 80px;
    @include font-mono;
    @include text-style(xs);
  }

  &__range {
    width: 100%;
    margin-bottom: spacing(1);
  }

  &__range-value {
    display: inline-block;
    padding: spacing(1) spacing(2);
    background: color(background, secondary);
    border-radius: map-get($border-radius, base);
    @include text-style(xs, medium);
    color: color(text, secondary);
  }

  &__checkbox-group {
    display: flex;
    flex-direction: column;
    gap: spacing(2);
  }

  &__checkbox-label {
    display: flex;
    align-items: center;
    gap: spacing(2);
    @include text-style(sm);
    color: color(text, secondary);
    cursor: pointer;
    margin-bottom: 0;

    &:hover {
      color: color(text, primary);
    }
  }

  &__checkbox {
    width: 16px;
    height: 16px;
    border: 1px solid color(border, medium);
    border-radius: map-get($border-radius, sm);
    cursor: pointer;
    
    &:checked {
      background: color(primary, 600);
      border-color: color(primary, 600);
    }
  }

  &__placeholder {
    text-align: center;
    color: color(text, tertiary);
    font-style: italic;
    padding: spacing(6) spacing(4);
  }
}

// Animation for panel entrance
.configuration-panel {
  @include slide-in-up(300ms);
}

@keyframes slideInFromRight {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}
