// Global Styles for Component Portfolio
// =====================================

@import 'tokens';
@import 'mixins';

// CSS Reset & Base Styles
// -----------------------
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
  scroll-behavior: smooth;
}

body {
  font-family: map-get(map-get($typography, font-families), primary);
  @include text-style(base, normal, normal);
  color: color(text, primary);
  background: color(background, primary);
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// Typography
// ----------
h1, h2, h3, h4, h5, h6 {
  @include font-display;
  font-weight: map-get(map-get($typography, font-weights), bold);
  line-height: map-get(map-get($typography, line-heights), tight);
  color: color(text, primary);
  margin-bottom: spacing(4);
}

h1 {
  @include text-style('5xl', bold, tight);
  
  @include responsive(md) {
    @include text-style('6xl', bold, tight);
  }
}

h2 {
  @include text-style('3xl', bold, tight);
  
  @include responsive(md) {
    @include text-style('4xl', bold, tight);
  }
}

h3 {
  @include text-style('2xl', semibold, tight);
  
  @include responsive(md) {
    @include text-style('3xl', semibold, tight);
  }
}

h4 {
  @include text-style('xl', semibold, tight);
  
  @include responsive(md) {
    @include text-style('2xl', semibold, tight);
  }
}

h5 {
  @include text-style('lg', medium, tight);
}

h6 {
  @include text-style('base', medium, tight);
}

p {
  margin-bottom: spacing(4);
  color: color(text, secondary);
  line-height: map-get(map-get($typography, line-heights), relaxed);
}

a {
  color: color(primary, 600);
  text-decoration: none;
  transition: transition(colors);
  
  &:hover {
    color: color(primary, 700);
    text-decoration: underline;
  }
  
  &:focus {
    @include focus-ring;
    border-radius: 2px;
  }
}

// Lists
// -----
ul, ol {
  margin-bottom: spacing(4);
  padding-left: spacing(6);
}

li {
  margin-bottom: spacing(1);
  color: color(text, secondary);
}

// Code
// ----
code {
  @include font-mono;
  @include text-style(sm);
  background: color(background, secondary);
  padding: spacing(1) spacing(2);
  border-radius: map-get($border-radius, sm);
  color: color(primary, 700);
}

pre {
  @include font-mono;
  @include text-style(sm);
  background: color(background, tertiary);
  padding: spacing(4);
  border-radius: map-get($border-radius, md);
  overflow-x: auto;
  margin-bottom: spacing(4);
  border: 1px solid color(border, light);
  
  code {
    background: none;
    padding: 0;
  }
}

// Form Elements
// -------------
input, textarea, select {
  @include input-base;
}

button {
  @include button-base;
}

// Utility Classes
// ---------------
.container {
  @include container;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.flex {
  display: flex;
}

.flex-center {
  @include flex-center;
}

.flex-between {
  @include flex-between;
}

.grid {
  display: grid;
}

.grid-center {
  @include grid-center;
}

.hidden {
  display: none;
}

.sr-only {
  @include visually-hidden;
}

// Spacing Utilities (Basic set only)
// ----------------------------------
.m-0 { margin: 0; }
.m-1 { margin: spacing(1); }
.m-2 { margin: spacing(2); }
.m-3 { margin: spacing(3); }
.m-4 { margin: spacing(4); }
.m-6 { margin: spacing(6); }
.m-8 { margin: spacing(8); }

.p-0 { padding: 0; }
.p-1 { padding: spacing(1); }
.p-2 { padding: spacing(2); }
.p-3 { padding: spacing(3); }
.p-4 { padding: spacing(4); }
.p-6 { padding: spacing(6); }
.p-8 { padding: spacing(8); }

.mt-0 { margin-top: 0; }
.mt-2 { margin-top: spacing(2); }
.mt-4 { margin-top: spacing(4); }
.mt-8 { margin-top: spacing(8); }

.mb-0 { margin-bottom: 0; }
.mb-2 { margin-bottom: spacing(2); }
.mb-4 { margin-bottom: spacing(4); }
.mb-8 { margin-bottom: spacing(8); }

// Animation Classes
// -----------------
.fade-in {
  @include fade-in;
}

.slide-in-up {
  @include slide-in-up;
}

.scale-in {
  @include scale-in;
}

// Component-specific Styles
// -------------------------
.card {
  @include card;
}

.card-elevated {
  @include card(true);
}

.card-purple {
  @include card-purple;
}

.btn-primary {
  @include button-primary;
}

.btn-secondary {
  @include button-secondary;
}

.btn-ghost {
  @include button-ghost;
}

.draggable {
  @include draggable-item;
}

.drop-zone {
  @include drop-zone;
}

.glass {
  @include glass-morphism;
}

.gradient-purple {
  @include gradient-purple;
}

.gradient-purple-light {
  @include gradient-purple-light;
}

.text-gradient-purple {
  @include text-gradient-purple;
}

// Loading States
// --------------
.loading {
  position: relative;
  overflow: hidden;
  
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      90deg,
      transparent,
      rgba(255, 255, 255, 0.6),
      transparent
    );
    animation: shimmer 2s infinite;
  }
}

@keyframes shimmer {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

// Scrollbar Styling
// -----------------
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: color(background, secondary);
}

::-webkit-scrollbar-thumb {
  background: color(border, medium);
  border-radius: map-get($border-radius, full);
  
  &:hover {
    background: color(primary, 400);
  }
}

// Selection Styling
// -----------------
::selection {
  background: color(primary, 200);
  color: color(primary, 900);
}

::-moz-selection {
  background: color(primary, 200);
  color: color(primary, 900);
}

// Focus Styles
// ------------
:focus-visible {
  @include focus-ring;
}

// Print Styles
// ------------
@media print {
  * {
    background: white !important;
    color: black !important;
    box-shadow: none !important;
  }
  
  .no-print {
    display: none !important;
  }
}
