@import '../../styles/mixins';

.hero {
  padding: spacing(20) 0 spacing(24);
  background: linear-gradient(135deg, 
    color(background, primary) 0%, 
    color(primary, 50) 100%
  );
  position: relative;
  overflow: hidden;
  
  // Unicorn Studio Background
  &__background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    pointer-events: none;
    
    // Blend with the existing gradient
    mix-blend-mode: multiply;
    
    // Ensure it covers the full hero area
    width: 100%;
    height: 100%;
    overflow: hidden;
    
    // Desktop background version
    &--desktop {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      
      // Target the canvas element inside Unicorn Studio
      canvas {
        height: 110% !important;
        opacity: 90%;
      }
      
      // Hide Unicorn Studio branding
      a {
        display: none !important;
      }
    }
  }

  // Background pattern (fallback)
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: radial-gradient(circle at 25% 25%, rgba(124, 58, 237, 0.1) 0%, transparent 50%),
                      radial-gradient(circle at 75% 75%, rgba(124, 58, 237, 0.05) 0%, transparent 50%);
    pointer-events: none;
    z-index: 1;
  }
  
  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: spacing(12);
    align-items: center;
    margin-bottom: spacing(16);
    position: relative;
    z-index: 2;
    
    @include responsive(lg) {
      gap: spacing(16);
    }
    
    @include tablet-up {
      grid-template-columns: 1fr;
      gap: spacing(12);
      text-align: center;
    }
  }
  
  &__text {
    z-index: 2;
    
    @include tablet-up {
      order: 2;
    }
  }
  
  &__title {
    @include text-style('5xl', bold, tight);
    color: color(secondary, 950);
    margin-bottom: spacing(6);

    & span {
      color: color(primary, 100);
    }
    
    @include responsive(lg) {
      @include text-style('6xl', bold, tight);
    }
    
    @include mobile-only {
      @include text-style('4xl', bold, tight);
    }
  }
  
  &__description {
    @include text-style('2xl', normal, relaxed);
    color: color(secondary, 950);
    margin-bottom: spacing(8);
    max-width: 600px;
    
    @include tablet-up {
      margin: 0 auto spacing(8);
    }
    
    @include mobile-only {
      @include text-style(lg, normal, relaxed);
    }
  }
  
  &__actions {
    @include flex-center;
    gap: spacing(4);
    
    @include tablet-up {
      justify-content: center;
    }
    
    @include mobile-only {
      flex-direction: column;
      align-items: stretch;
    }
  }
  
  &__visual {
    position: relative;
    z-index: 2;
    
    @include tablet-up {
      order: 1;
      max-width: 500px;
      margin: 0 auto;
    }
  }
  
  &__card {
    background: white;
    border-radius: map-get($border-radius, xl);
    box-shadow: shadow(xl);
    border: 1px solid color(border, light);
    
    &--main {
      position: relative;
      z-index: 1;
    }
    
    &--floating {
      position: absolute;
      background: rgba(255, 255, 255, 0.95);
      backdrop-filter: blur(10px);
      box-shadow: shadow(lg);
      
      &.hero__card--top {
        top: -spacing(4);
        right: -spacing(6);
        width: 120px;
        height: 80px;
        padding: spacing(3);
        
        @include mobile-only {
          top: -spacing(2);
          right: -spacing(3);
          width: 100px;
          height: 70px;
          padding: spacing(2);
        }
      }
      
      &.hero__card--bottom {
        bottom: -spacing(4);
        left: -spacing(6);
        width: 100px;
        height: 60px;
        padding: spacing(3);
        
        @include mobile-only {
          bottom: -spacing(2);
          left: -spacing(3);
          width: 80px;
          height: 50px;
          padding: spacing(2);
        }
      }
    }
  }
  
  &__card-header {
    @include flex-between;
    padding: spacing(4) spacing(6);
    border-bottom: 1px solid color(border, light);
    
    @include mobile-only {
      padding: spacing(3) spacing(4);
    }
  }
  
  &__card-dots {
    @include flex-center;
    gap: spacing(2);
    
    span {
      width: 8px;
      height: 8px;
      border-radius: 50%;
      
      &:nth-child(1) { background: #ff5f57; }
      &:nth-child(2) { background: #ffbd2e; }
      &:nth-child(3) { background: #28ca42; }
    }
  }
  
  &__card-title {
    @include text-style(sm, medium);
    color: color(text, secondary);
  }
  
  &__card-content {
    padding: spacing(6);
    
    @include mobile-only {
      padding: spacing(4);
    }
  }
  
  &__demo-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: spacing(3);
  }
  
  &__demo-item {
    height: 40px;
    background: linear-gradient(135deg, color(primary, 400), color(primary, 600));
    border-radius: map-get($border-radius, md);
    
    &:nth-child(1) { height: 30px; }
    &:nth-child(2) { height: 50px; }
    &:nth-child(3) { height: 35px; }
    &:nth-child(4) { height: 45px; }
    &:nth-child(5) { height: 25px; }
    &:nth-child(6) { height: 55px; }
  }
  
  &__mini-chart {
    width: 100%;
    height: 100%;
    @include flex-center;
    align-items: flex-end;
  }
  
  &__mini-bars {
    @include flex-center;
    align-items: flex-end;
    gap: 2px;
    height: 100%;
    width: 100%;
  }
  
  &__mini-bar {
    flex: 1;
    background: linear-gradient(to top, color(primary, 500), color(primary, 400));
    border-radius: 1px;
    min-height: 8px;
  }
  
  &__progress-rings {
    @include flex-center;
    gap: spacing(2);
    width: 100%;
    height: 100%;
  }
  
  &__progress-ring {
    flex-shrink: 0;
  }
  
  &__features {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: spacing(8);
    position: relative;
    z-index: 2;
    
    @include mobile-only {
      grid-template-columns: 1fr;
      gap: spacing(6);
    }
  }
  
  &__feature {
    @include flex-center;
    align-items: flex-start;
    gap: spacing(4);
    padding: spacing(6);
    background: white;
    border-radius: map-get($border-radius, xl);
    box-shadow: shadow(sm);
    border: 1px solid color(border, light);
    transition: transition(all);
    
    &:hover {
      box-shadow: shadow(md);
      transform: translateY(-2px);
    }
    
    @include mobile-only {
      flex-direction: column;
      text-align: center;
      padding: spacing(8) spacing(6);
    }
  }
  
  &__feature-icon {
    @include flex-center;
    width: 60px;
    height: 60px;
    background: color(primary, 100);
    color: color(primary, 600);
    border-radius: map-get($border-radius, xl);
    flex-shrink: 0;
  }
  
  &__feature-content {
    flex: 1;
  }
  
  &__feature-title {
    @include text-style(xl, semibold, tight);
    color: color(text, primary);
    margin-bottom: spacing(2);
  }
  
  &__feature-description {
    @include text-style(base, normal, relaxed);
    color: color(text, secondary);
    margin: 0;
  }
}
